    <div class="submission">
        <!-- 占位 -->
        <div style="height:170px;"></div> 
        <div class="upload-container" id="drop_area"  >   
            <div class="dropUp"  >
                <el-upload
                    class="upload-demo"
                    action="http://120.26.95.9:8081/upload/"
                    :accept="'.zip'"
                    :on-change="handleUpload" 
                    :on-success="handleSubmitSuccess"
                    :on-error="handleSubmitError"
                    :auto-upload="false"
                    :file-list="fileList"
                    multiple
                    drag
                    ref="el"
                    :limit="1">
                    <img src="../../assets/images/云.svg" >
                    <div style="color:white;font-size: large;font-weight: bold;">上传文件</div><br/>
                    <div class="upload__text">点击图标或将文件文件拖拽到此处上传</div>
                    <div class="filestyle">支持文件格式：zip等</div>
                </el-upload>
            </div>        
        </div>
        <!-- 占位 -->
        <div style="height:25px;"></div>
        <div class="report" style="height: 10%; margin-top: 1.5%;">
                <el-button  class="btn-sub"  size="medium" @click="postFiles">开始分析</el-button>
        </div>

        <div class="below" style="height: 30%; margin-top: 1.5%;">
            <div class="table">
                <table>
                    <caption>最近提交</caption>
                    <thead>
                        <tr>
                            <th style="border: 1px solid #7400bf;">应用名称</th>
                            <th style="border: 1px solid #8946b8;">版本</th>
                            <th style="border: 1px solid #ac7eb0;">上传时间</th>
                            <th style="border: 1px solid  #df8cab;">上传用户</th>
                            <th style="border: 1px solid #ff7bab;">操作</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td style="border: 1px solid #7400bf;"><input type="radio" name="Appname" value="DEMO" v-model="selectDemo"><span>dubbo</span></td>
                            <td style="border: 1px solid #8946b8;">2.6.11</td>
                            <td style="border: 1px solid #ac7eb0;">2023.8.15 21:00</td>
                            <td style="border: 1px solid  #df8cab;">ADMIN</td>
                            <td style="border: 1px solid #ff7bab;">
                                <button class="btn-sc" style="border:none;background:none;cursor:pointer;" @click="this.$router.push('/console/overview')">
                                    <span style="color: #ff7bab;">查看</span>
                                </button>
                                <button class="btn-sc" style="border:none;background:none;cursor:pointer;" @click="downloaddoc1">
                                    <span style="color: #ff7bab;">下载</span>
                                </button>
                                <button class="btn-sc" style="border:none;background:none;"><span style="color: red;">删除</span></button>
                            </td>
                        </tr>
                    </tbody>
                </table>
