</template>


<script>
export default {
    data(){
        return {
            selectDemo: false,
            fileList:[],

        }
    },

    methods: {
        downloaddoc1() {
            let a = document.createElement("a");
            a.href = "/static/ReportDownload.docx";
            a.click();
        },
        handleAlert(){
            alert('您还不是会员');
            return false;
        },
        goToList(){
            if(this.selectDemo){
                this.$router.push('/submission/reportList');
            }
            else{
                alert('您还没有选中的项目！')
            }
        },

        handleUpload(file) {
            if(file.status!="ready")
                return;
            const isAPK = file.raw.type === 'application/x-zip-compressed';
            if(isAPK){
                console.log(file.raw.name + "上传成功");
                this.fileList.push(file);
                this.$message({
                        message: '上传成功',
                        type: 'success'
                    });
                    setTimeout(() => {
                        this.$message.closeAll();
                    }, 2000
                );
            }
            else{
                this.fileList=[];
                this.$message.error('仅限上传zip等压缩格式文件');
            }
            
        },
        // 真正向后端提交
        postFiles() {
            if(this.fileList.length === 0){
                alert('请上传文件');
                return;
            }
            this.$refs.el.submit();  
        },
        // 处理提交结果
        handleSubmitSuccess(){
            this.$message({
                message: '提交成功',
                type: 'success'
            });
            this.$message.info('即将跳转');
            setTimeout(()=>{
                this.$router.push('/console/detail')
            },3000);
        },
        handleSubmitError(){
            this.$message({
                message: '服务器繁忙，请稍后再试~',
                type: 'error'
            });
        },    
    },
}
</script>

